                                    </v-col>
                                </v-row>
                                <v-row>
                                    <div class="row-with-line"></div>
                                    <v-col style="padding: 0;">
                                        <v-col class="element"
                                               @click="setLabelPosition(myType['Label Convenient'])">
                                            Convenient
