                    <!-- <v-expansion-panel>
                        <v-expansion-panel-title>
                            Print
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-container class="container">
                                <v-row>
                                    <input type="button" id="myButtonPrint" class="colorfulButton" v-model=printText
                                           @click="printSVG()"/>
                                </v-row>
